import React from "react";
import "./App.css";
import { Project, projects, ProjectType } from "./projects";

function App() {
  return (
    <div className='App'>
      <h1>ðŸš‚ derail</h1>
      <div id='projects'>
        <Projects type={ProjectType.Code} />
        <Projects type={ProjectType.Contributions} />
        <Projects type={ProjectType.Presentations} />
        <Projects type={ProjectType.Other} />
      </div>
    </div>
  );
}

const Projects = ({ type }: PropProjectType) => {
  return (
    <div className='project'>
      <h2>{type}</h2>
      {projects
        .filter((project) => project.type === type)
        .sort((a, b) => {
          if (a.title.toLowerCase() < b.title.toLowerCase()) {
            return -1;
          }
          if (b.title.toLowerCase() > a.title.toLowerCase()) {
            return 1;
          }
          return 0;
        })
        .map((project) => {
          return (
            <span key={project.title} className='project'>
              <a href={project.url}>{project.title}</a>
              <p>{project.description}</p>
            </span>
          );
        })}
    </div>
  );
};

export interface PropProjectType {
  type: ProjectType;
}

export default App;
